import {
    SUGGEST_ITEM_HEIGHT,
    SUGGEST_ITEM_MARGIN,
    AddressSimpleSuggestItemView,
} from 'pages/AddressSimple/SuggestItem/AddressSimpleSuggestItem';

import { Reducer, AppState, initialState } from '../../constants';

const prepareSuggestItems = (state: AppState) => {
    const {
        addressSimplePage: { inputValue, suggestsRaw },
    } = state;

    return suggestsRaw.reduce<AddressSimpleSuggestItemView[]>((result, suggestItem) => {
        const { name, id } = suggestItem;

        if (name !== inputValue) {
            result.push({
                id,
                name,
            });
        }

        return result;
    }, []);
};

const prepareIsSubmitButtonDisabled = ({ user }: AppState) => {
    return (
        !user.address.street ||
        !user.address.house ||
        !user.address.city ||
        // eslint-disable-next-line @typescript-eslint/camelcase
        !user.address.address_string ||
        !user.address.location.latitude ||
        !user.address.location.longitude
    );
};

const prepareSuggestListContainerYShift = ({ addressSimplePage: { selectedSuggestItemIndex, suggests } }: AppState) => {
    return (
        (SUGGEST_ITEM_HEIGHT + SUGGEST_ITEM_MARGIN) *
        Math.min(suggests.length - 3, Math.max(0, selectedSuggestItemIndex - 1))
    );
};

export const addressSimplePageView: Reducer = (state, action) => {
    const { addressSimplePage } = state;

    switch (action.type) {
        case 'address/CHANGE_INPUT_VALUE': {
            addressSimplePage.inputValue = action.payload;
            addressSimplePage.selectedSuggestItemIndex = initialState.addressSimplePage.selectedSuggestItemIndex;

            break;
        }

        case 'address/SET_SUGGESTS': {
            addressSimplePage.suggestsRaw = action.payload;

            break;
        }

        case 'address/SELECT_SUGGEST_ITEM': {
            addressSimplePage.selectedSuggestItemIndex = action.payload;

            break;
        }

        case 'address/CLICK_SUGGEST_ITEM': {
            const clickedSuggestItem = addressSimplePage.suggests[action.payload];

            addressSimplePage.inputValue = clickedSuggestItem.name;
            addressSimplePage.selectedSuggestItemIndex = initialState.addressSimplePage.selectedSuggestItemIndex;

            break;
        }

        case 'address/FOCUS_INPUT': {
            addressSimplePage.isInputFocused = true;

            break;
        }

        case 'address/BLUR_INPUT': {
            addressSimplePage.isInputFocused = false;

            break;
        }

        case 'ADDRESS_INPUT': {
            // TODO: 
            addressSimplePage.inputValue = action.payload.input || '';
            // addressSimplePage.deviceLocation = action.payload.device_location;

            break;
        }

        case 'ADDRESS_INPUT_ADD': {
            const lastSymbol = addressSimplePage.inputValue[addressSimplePage.inputValue.length - 1];
            const prefix = lastSymbol === ' ' ? '' : ' ';

            addressSimplePage.inputValue += `${prefix}${action.payload.input} `;

            break;
        }

        default:
            break;
    }

    addressSimplePage.suggests = prepareSuggestItems(state);
    addressSimplePage.suggestListContainerYShift = prepareSuggestListContainerYShift(state);
    addressSimplePage.isSubmitButtonDisabled = prepareIsSubmitButtonDisabled(state);
};
