import * as DG from '2gis-maps';
import { makeRequest } from 'tools/request';
import { Location } from 'store/constants/user';

const DG_KEY = 'ruvqwj0703';

interface GetSuggestsParams {
    q: string;
    location: Location;
}
export const getSuggests = ({ q, location: { longitude, latitude } }: GetSuggestsParams): Promise<DG.SuggestItem[]> => {
    if (q === '') {
        return Promise.resolve([] as DG.SuggestItem[]);
    }

    return Promise.resolve(0)
        .then(() => {
            const params = {
                q,
                key: DG_KEY,
                local: 'ru_RU',
                fields: [
                    'items.address',
                    // 'items.statistics',
                    // 'items.name_ex',
                    'items.point',
                    // 'items.description',
                    // 'items.context',
                ].join(','),
                type: ['building', 'street'].join(','),
                viewpoint1: [(Number(longitude) - 0.2).toFixed(6), (Number(latitude) + 0.2).toFixed(6)].join(','),
                viewpoint2: [(Number(longitude) + 0.2).toFixed(6), (Number(latitude) - 0.2).toFixed(6)].join(','),
                // eslint-disable-next-line @typescript-eslint/camelcase
                is_viewport_change: false,
            };

            return makeRequest('https://catalog.api.2gis.ru/3.0/suggests', params);
        })
        .then((response) => {
            if (response.meta.code !== 200) {
                console.error(response.meta.error);
            }

            return response;
        })
        .then(({ result }: DG.SuggestResponse) => {
            if (!result) {
                return [];
            }

            return result.items;
            // return result.items.filter((item) => item.type === 'street');
        })
        .then((data) => {
            console.log('some', data);

            return data;
        });
};

interface SearchParams {
    point: string;
}

export const search = ({ point }: SearchParams) => {
    const params = {
        key: DG_KEY,
        point,
        radius: '30',
        type: 'building',
    };

    return makeRequest('https://catalog.api.2gis.ru/2.0/geo/search', params).then((response: DG.SearchResponse) => {
        if (response.meta.code !== 200) {
            console.error(response.meta.error);
        }

        return response;
    });
};

interface GetItemsByIdParams {
    id: string;
}
export const getItemsById = ({ id }: GetItemsByIdParams) => {
    const params = {
        key: DG_KEY,
        id,
        fields: ['items.name_ex'].join(','),
    };
};
